body {
  font-family: 'Poppins', sans-serif;
  font-size: 1.15rem;
  margin: 0;
  overflow-x: hidden;
}

/*navbar*/
.navbar-nav {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
}

.bg-light {
  background: #fff;
}

.navbar-light .navbar-nav .nav-link {
  color: #543552;
}

.navbar-light .navbar-brand {
  padding-left: 11rem;
}

.nav-not-signedin {
  display: flex;
  padding-left: 68%;
}

/*home page*/
.homepage-header {
  font-size: 60px;
}

/*form button*/
.save-btn {
  background-color: black;
  color: pink;
  width: 100%;
  margin: 1.9rem 0 0.1rem;
}

/*profile*/
.greeting {
  color: #fff;
  font-size: 4rem;
  font-weight: 600;
  padding-top: 3rem;
  margin-bottom: 0;
  background: #41334d;
}

.profile-row {
  background: #41334d;
}

.create {
  color: pink;
  padding: 0.8rem 0 3.2rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.info-col {
  background: #41334d;
  margin: 5.9rem 0 0 2rem;
  border-radius: 6px;
}

.form-col {
  padding: 5.2rem 0;
  color: #fff;
}

.form-header {
  color: blue;
}

.group {
  display: flex;
  align-items: center;
}

.group .list-group-item {
  background: linear-gradient(
    60deg,
    rgb(65, 51, 77) 0%,
    #bfa4bd 50%,
    rgb(65, 51, 77) 100%
  ) !important;
  border-radius: 3px;
  height: 5.3rem;
  display: flex;
  align-items: center;
}

.list-group-item a {
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 600;
}

/*video*/
.video-row {
  padding: 1.7rem 0;
  border: solid 1px #ccc;
  border-radius: 5px;
}

.col-md-8,
.col-md-4 {
  padding-left: 0;
  padding-right: 0;
}

.video-card {
  border: none;
}

.video-form,
.video-search {
  display: flex;
}

.video-search {
  margin: 2.7rem 0;
  max-width: 100%;
}

.url-input {
  margin-left: 0.4rem;
}

.add-video-header {
  color: #804a75;
  display: flex;
  margin-top: 0.4rem;
  margin-bottom: 0.15;
}

.add-video-btn,
.search-btn {
  max-width: 92.5%;
  margin: 0 0 0 0.4rem;
}

.video-title {
  margin-top: 0.7rem;
}

.delete {
  padding-top: 0.1rem;
  font-size: 3rem;
}

.delete-video {
  padding-top: 0.5rem;
  font-size: 1.4rem;
}

/*vision board*/
.vision-board-form-container {
  display: flex;
  margin-left: 1.3rem;
}

/*vision header*/
.add-board-btn,
.add-image-btn {
  background: #fff;
  color: #000;
  margin-top: 1rem;
  position: absolute;
  left: 1.5rem;
  top: -0.3rem;
}

.vision-board-delete {
  color: #ccc;
}

.add-imgbox-btn,
.save-img-btn,
.search-btn {
  background: #000;
  color: pink;
}

.add-imgbox-btn {
  margin-top: 1rem;
}

.save-img-btn {
  margin-bottom: 1rem;
  width: 100%;
}

.img-card p {
  color: #543552;
  font-size: 20px;
  margin: 0.2rem 0.7rem;
}

.img-text {
  width: 100%;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.text-trash {
  display: flex;
  justify-content: space-evenly;
}

.title-trash {
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
}

.boards {
  background: #f2f5f5;
}

.vision-board {
  background: #fff;
  border-radius: 6px;
  box-shadow: 0 2px 5px #ccc;
  height: 94vh;
  margin: 0 1.5rem;
  padding: 0.5rem;
}

.vision-board-title {
  padding-top: 1rem;
  font-size: 1.5rem;
}

.add-images-instructions h3 {
  padding-top: 3rem;
  color: #fff;
  font-weight: 900;
  font-size: 40px;
}

.add-images-instructions p {
  font-size: 2rem;
  font-weight: 900;
  letter-spacing: 0.17rem;
  color: silver;
  background: #000;
}

.vision-board-instructions {
  padding-top: 4rem;
  color: #000;
}

.vision-board-instructions h1 {
  font-size: 35px;
  margin-bottom: 1.3rem;
}

.vision-board-instructions p {
  color: #000;
  font-size: 25px;
}

/*footer*/
footer {
  background: #41334d;
  height: 8rem;
}

.copyright {
  margin-top: 4rem;
}

@media (max-width: 506px) {
  .video-row {
    border: none;
  }
  .search-btn {
    margin-top: 1rem;
  }
  .save-btn {
    margin-right: 0;
  }
}

@media (max-width: 575px) {
  .add-video-header {
    display: block;
  }
  .video-inputs-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
  }
  /* .form-control {
    margin-right: 0;
  }
  .add-video-container input {
    //width: 100vw;
    margin: 0 auto;
  }*/
  .add-video-btn {
    //width: 100vw;
    //margin: 0 auto;
  }
  .mb-3 {
    margin: 0 auto;
    //width: 100vw;
  }
  .video-form {
    display: flex;
    flex-direction: column;
    margin: auto;
  }
  .video-search {
    align-items: flex-start;
    flex-direction: column;
    width: 100vw;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .profile {
    width: 100vw;
    display: flex;
    flex-direction: column;
    backgroun: #fff;
  }
  .col-md-8 {
    margin-top: 3rem;
  }
  .greeting {
    font-size: 2.5rem;
  }
  .create {
    font-size: 1.8rem;
    padding-top: 0;
    width: 88vw;
  }
  .profile-update-form {
    margin: 0 0.2rem;
  }
  .group {
    width: 88vw;
  }
  .group .link {
    font-size: 1.3rem;
  }
  .form-col.profile-update-form {
    align-items: center;
    width: 88vw;
    padding: 0 1rem 1rem;
    margin: auto;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .video-col {
    width: 100vw;
  }
}
